<template>
  <b-container>
    <b-row>
      <b-col cols="12" sm="3">
        <img
          :src="require(`@/assets/images/${itmOrder.img}`)"
          class="w-75 rounded img-thumbnail d-block mx-auto"
        />
      </b-col>
      <b-col cols="12" sm="9" class="h-100">
        <h2>{{ itmOrder.title }}</h2>
        <b-list-group>
          <b-list-group-item>
            Available kilo/s: {{ availableKg }}
          </b-list-group-item>
          <b-list-group-item>
            Kilo/s ordered to dish: {{ kgOrder }}
          </b-list-group-item>
          <b-list-group-item>
            Selected Restaurant: {{ restaurant.title }}
          </b-list-group-item>
        </b-list-group>

        <b-form-group label="Dine in or Pa Deliver">
          <b-form-radio v-model="acquire" name="acquire" value="dine-in">
            Dine In
          </b-form-radio>
          <b-form-radio v-model="acquire" name="acquire" value="deliver">
            Deliver
          </b-form-radio>
        </b-form-group>
        <h3>{{ enterInfo }}</h3>
        <p><small>All entries below are required.</small></p>
        <b-alert show variant="warning" v-if="errors.length">
          <p>Please correct the following errors</p>
          <b-list-group>
            <b-list-group-item v-for="(error, index) in errors" :key="index">
              {{ error }}
            </b-list-group-item>
          </b-list-group>
        </b-alert>
        <b-form-group id="Customer-Group" label="Name" label-for="customerName">
          <b-form-input
            id="customerName"
            v-model="customerName"
            required
            placeholder="Enter your name"
          ></b-form-input>
        </b-form-group>
        <b-form-group
          id="ContactNo"
          label="Contact No"
          label-for="contactNumber"
        >
          <b-form-input
            id="contactNumber"
            v-model="contactNumber"
            placeholder="Enter your contact number"
          ></b-form-input>
        </b-form-group>
        <b-form-group
          id="acquireDateGrp"
          :label="acquireDateLabel"
          label-for="acquireDate"
        >
          <b-form-input
            id="acquireDate"
            v-model="acquireDate"
            type="date"
          ></b-form-input>
        </b-form-group>
        <b-form-group
          id="acquireTimeGrp"
          :label="acquireTimeLabel"
          label-for="acquireTime"
        >
          <b-form-input
            id="acquireTime"
            v-model="acquireTime"
            type="time"
          ></b-form-input>
        </b-form-group>
        <div>
          <b-button block class="mb-2" variant="outline-dark" to="/palengke">
            Visit our palengke
          </b-button>
          <b-button block class="mb-2" variant="outline-dark" to="/paluto">
            Choose from our paluto
          </b-button>
          <b-button block class="mb-5" variant="dark" @click="prepareNext">
            Add to cart <font-awesome-icon icon="cart-plus"></font-awesome-icon>
          </b-button>
        </div>
        <b-table-simple stacked="lg">
          <b-thead>
            <b-tr>
              <b-th>#</b-th>
              <b-th>Pic</b-th>
              <b-th>Dish</b-th>
              <b-th>Kg Weight</b-th>
              <b-th>Special Instruction</b-th>
              <b-th>Market Price</b-th>
              <b-th>Paluto Price</b-th>
            </b-tr>
          </b-thead>
          <b-tbody>
            <b-tr v-for="dsh in dishes" :key="dsh.id">
              <b-td stacked-heading="#">{{ dsh.id }}</b-td>
              <b-td class="p-0 cell-img">
                <img
                  :src="require(`@/assets/images/${dsh.img}`)"
                  class="w-100 rounded img-thumbnail d-inline"
                />
                <slot></slot>
              </b-td>
              <b-td stacked-heading="Dish">{{ dsh.dish }}</b-td>
              <b-td stacked-heading="Kg Weight">{{ dsh.kg }}</b-td>
              <b-td stacked-heading="Special Instruction">{{ dsh.si }}</b-td>
              <b-td stacked-heading="Market Price">{{ dsh.mp }}</b-td>
              <b-td stacked-heading="Paluto Price">{{ dsh.pp }}</b-td>
            </b-tr>
          </b-tbody>
        </b-table-simple>
      </b-col>
    </b-row>
  </b-container>
</template>
<script>
import {
  BContainer,
  BRow,
  BCol,
  BFormGroup,
  BFormRadio,
  BFormInput,
  BButton,
  BListGroup,
  BListGroupItem,
  BTableSimple,
  BTr,
  BTh,
  BTd,
  BThead,
  BTbody,
  BAlert
} from "bootstrap-vue";
import { FontAwesomeIcon } from "@fortawesome/vue-fontawesome";

export default {
  name: "DishRestaurantSummaryDetails",
  props: [
    "itmOrder",
    "selectedDish",
    "availableKg",
    "kgOrder",
    "selectedRestaurant"
  ],
  data: function() {
    return {
      acquire: "dine-in",
      customerName: null,
      contactNumber: null,
      acquireDate: null,
      acquireTime: null,
      errors: []
    };
  },
  mounted() {
    document.querySelector("#pre-header").scrollIntoView({
      behavior: "smooth",
      alignToTop: true
    });
  },
  computed: {
    restaurant() {
      return JSON.parse(this.selectedRestaurant);
    },
    dishes() {
      return JSON.parse(this.selectedDish);
    },
    enterInfo() {
      if (this.acquire == "dine-in") return "Enter Dine In Info";
      else return "Enter Deliver Info";
    },
    acquireDateLabel() {
      if (this.acquire == "dine-in") return "Enter Dine In Date";
      else return "Enter Delivery Date";
    },
    acquireTimeLabel() {
      if (this.acquire == "dine-in") return "Enter Dine In Time";
      else return "Enter Delivery Time";
    }
  },
  components: {
    "b-container": BContainer,
    "b-row": BRow,
    "b-col": BCol,
    "b-list-group": BListGroup,
    "b-list-group-item": BListGroupItem,
    "b-form-group": BFormGroup,
    "b-form-radio": BFormRadio,
    "b-form-input": BFormInput,
    "b-button": BButton,
    "b-table-simple": BTableSimple,
    "b-tr": BTr,
    "b-th": BTh,
    "b-td": BTd,
    "b-thead": BThead,
    "b-tbody": BTbody,
    FontAwesomeIcon,
    "b-alert": BAlert
  },
  methods: {
    prepareNext() {
      this.errors = [];
      if (
        this.customerName &&
        this.contactNumber &&
        this.acquireDate &&
        this.acquireTime
      ) {
        let details = Object.create(null);
        details.name = this.customerName;
        details.contact = this.contactNumber;
        details.date = this.acquireDate;
        details.time = this.acquireTime;
        details.mode = this.acquire;
        this.$emit("pass-details", details);
      } else {
        if (!this.customerName) this.errors.push("Name is required");
        if (!this.contactNumber) this.errors.push("Contact number is required");
        if (!this.acquireDate) this.errors.push("Date is required");
        if (!this.acquireTime) this.errors.push("Time is required");
      }
    }
  }
};
</script>
<style>
.cell-img {
  width: 300px;
}
@media only screen and (min-width: 768px) {
  .cell-img {
    width: 150px;
  }
}
</style>
