<template>
  <b-container>
    <b-row>
      <b-col cols="12" sm="3">
        <img
          :src="require(`@/assets/images/${itmOrder.img}`)"
          class="w-75 rounded img-thumbnail d-block mx-auto"
        />
      </b-col>
      <b-col cols="12" sm="9" class="h-100">
        <h2>Dish select for {{ itmOrder.title }}</h2>
        <b-list-group>
          <b-list-group-item v-if="itmOrderDetails.prod_details">
            {{ itmOrderDetails.prod_details }}
          </b-list-group-item>
          <b-list-group-item>
            Price per kilo: &#8369; {{ itmOrderDetails.unitPrice }}
          </b-list-group-item>
          <b-list-group-item>
            Selected weight in kilo/s: {{ itmOrderDetails.weight }}
          </b-list-group-item>
          <b-list-group-item>
            Order price: &#8369; {{ itmOrderDetails.marketPrice }}
          </b-list-group-item>
          <b-list-group-item>
            Available kilo/s: {{ availableKg }}
          </b-list-group-item>
          <b-list-group-item>
            Kilo/s ordered to dish: {{ kgOrder }}
          </b-list-group-item>
        </b-list-group>
        <h2 class="mt-3">Dish selected</h2>
        <b-button
          block
          class="mb-2"
          variant="outline-dark"
          @click="chooseDish"
          :disabled="availableKgStatus"
        >
          Choose from dish list
        </b-button>
        <b-button
          block
          class="mb-2"
          variant="dark"
          @click="submitDish"
        >
          Continue to order
        </b-button>
        <b-table-simple stacked="lg">
          <b-thead>
            <b-tr>
              <b-th>#</b-th>
              <b-th>Pic</b-th>
              <b-th>Dish</b-th>
              <b-th>Kg Weight</b-th>
              <b-th>Special Instruction</b-th>
              <b-th>Market Price</b-th>
              <b-th>Paluto Price</b-th>
            </b-tr>
          </b-thead>
          <b-tbody>
            <b-tr v-for="dsh in selectedDish" :key="dsh.id">
              <b-td stacked-heading="#">{{ dsh.id }}</b-td>
              <b-td class="p-0 cell-img">
                <img
                  :src="require(`@/assets/images/${dsh.img}`)"
                  class="w-100 rounded img-thumbnail d-inline"
                />
                <slot></slot>
              </b-td>
              <b-td stacked-heading="Dish">{{ dsh.dish }}</b-td>
              <b-td stacked-heading="Kg Weight">{{ dsh.kg }}</b-td>
              <b-td stacked-heading="Special Instruction">{{ dsh.si }}</b-td>
              <b-td stacked-heading="Market Price">{{ dsh.mp }}</b-td>
              <b-td stacked-heading="Paluto Price">{{ dsh.pp }}</b-td>
            </b-tr>
          </b-tbody>
        </b-table-simple>
        <div id="select-below"></div>
        <transition name="fade">
          <b-row v-if="orderDish" class="ml-1">
            <h2 class="d-block w-100">Select from dish below</h2>
            <h3 class="d-block w-100">
              <span
                class="d-inline-block mr-3"
                v-for="(mode, index) in displayModes"
                :key="index"
              >
                <a
                  href="#"
                  class="text-secondary"
                  :class="[{ 'text-dark': currentMode == mode.display }]"
                  @click.prevent="switchMode(index)"
                >
                  <font-awesome-icon :icon="mode.icon"></font-awesome-icon>
                </a>
              </span>
            </h3>
            <transition name="fade">
              <div v-show="isGrid">
                <ProductsGridWPrice
                  :items="dishes"
                  label="Select"
                  @next-step="proceedNext"
                />
              </div>
            </transition>
            <transition name="fade">
              <div v-show="isList">
                <ProductsListWPrice
                  :items="dishes"
                  label="Select"
                  @next-step="proceedNext"
                />
              </div>
            </transition>
            <b-col cols="12">
              <b-form-group
                id="dish-name-group"
                label="Selected Dish"
                label-for="selectedDishTitle"
              >
                <b-form-input
                  id="selectedDishTitle"
                  v-model="selectedDishTitle"
                  placeholder="Select dish from the list"
                  disabled
                ></b-form-input>
              </b-form-group>
              <b-form-group
                id="dish-price-group"
                label="Selected Dish Price"
                label-for="selectedDishPrice"
              >
                <b-form-input
                  id="selectedDishPrice"
                  v-model="selectedDishPrice"
                  placeholder="Select dish price from the list"
                  disabled
                ></b-form-input>
              </b-form-group>
              <label for="kilo">Select kg weight below</label>
              <b-form-select
                v-model="selectedDishWeight"
                :options="availableKgArray"
                class="mb-3"
              ></b-form-select>
              <b-form-group
                id="special-instruction-group"
                label="Special instruction"
                label-for=""
              >
                <b-form-textarea
                  id="specialInstruction"
                  v-model="specialInstruction"
                  placholder="Special instruction, for example: extra spicy"
                ></b-form-textarea>
              </b-form-group>
              <b-button
                block
                class="mb-2"
                variant="outline-dark"
                @click="closeDishChoice"
              >
                Cancel
              </b-button>
              <b-button
                block
                class="mb-2"
                variant="dark"
                :disabled="dishIsSelected"
                @click="addToDishList"
              >
                Add to dish list
              </b-button>
            </b-col>
          </b-row>
        </transition>
      </b-col>
    </b-row>
  </b-container>
</template>
<script>
import {
  BContainer,
  BRow,
  BCol,
  BListGroup,
  BListGroupItem,
  BButton,
  BTableSimple,
  BTr,
  BTh,
  BTd,
  BThead,
  BTbody,
  BFormGroup,
  BFormInput,
  BFormSelect,
  BFormTextarea
} from "bootstrap-vue";
import ProductsGridWPrice from "@/components/ProductsGridWPrice.vue";
import ProductsListWPrice from "@/components/ProductsListWPrice.vue";
import { FontAwesomeIcon } from "@fortawesome/vue-fontawesome";

export default {
  name: "dishselect",
  props: ["itmOrder", "itmOrderDetails", "dishes"],
  data: function() {
    return {
      availableKg: this.itmOrderDetails.weight,
      kgOrder: 0,
      orderDish: false,
      selectedDish: [],
      selectedDishTitle: null,
      selectedDishPrice: null,
      selectedDishWeight: 0.5,
      selectedDishImg: null,
      specialInstruction: "",
      currentMode: "list",
      displayModes: [
        { display: "list", icon: "th-list" },
        { display: "grid", icon: "th" }
      ]
    };
  },
  computed: {
    isGrid() {
      return this.currentMode == "grid";
    },
    isList() {
      return this.currentMode == "list";
    },
    availableKgArray() {
      let initArr = [];
      if (this.availableKg >= 0.5) {
        for (var i = 0.5; i <= this.availableKg; i += 0.5) {
          initArr.push(i);
        }
      }

      return initArr;
    },
    availableKgStatus() {
      return !(this.availableKg > 0);
    },
    dishIsSelected() {
      return !this.selectedDishTitle;
    }
  },
  components: {
    "b-container": BContainer,
    "b-row": BRow,
    "b-col": BCol,
    "b-list-group": BListGroup,
    "b-list-group-item": BListGroupItem,
    "b-button": BButton,
    "b-table-simple": BTableSimple,
    "b-tr": BTr,
    "b-th": BTh,
    "b-td": BTd,
    "b-thead": BThead,
    "b-tbody": BTbody,
    "b-form-group": BFormGroup,
    "b-form-input": BFormInput,
    "b-form-select": BFormSelect,
    "b-form-textarea": BFormTextarea,
    ProductsGridWPrice,
    ProductsListWPrice,
    FontAwesomeIcon
  },
  methods: {
    switchMode(index) {
      this.currentMode = this.displayModes[index].display;
    },
    chooseDish() {
      //clear:
      this.selectedDishTitle = null;
      this.selectedDishPrice = null;
      this.selectedDishWeight = 0.5;
      this.specialInstruction = null;

      this.orderDish = true;

      document.querySelector("#select-below").scrollIntoView({
        behavior: "smooth",
        alignToTop: true
      });
    },
    proceedNext(orderParams) {
      this.selectedDishTitle = orderParams.title;
      this.selectedDishPrice = orderParams.prize;
      this.selectedDishImg = orderParams.img;

      document.querySelector("#specialInstruction").scrollIntoView({
        behavior: "smooth"
      });
    },
    closeDishChoice() {
      this.orderDish = false;
    },
    addToDishList() {
      this.availableKg -= this.selectedDishWeight;
      this.kgOrder += this.selectedDishWeight;

      let offset = this.selectedDish.length + 1;

      let objDish = Object.create(null);
      objDish.id = offset;
      objDish.img = this.selectedDishImg;
      objDish.dish = this.selectedDishTitle;
      objDish.kg = this.selectedDishWeight;
      objDish.si = this.specialInstruction;
      objDish.mp = parseFloat(this.itmOrderDetails.unitPrice) * parseFloat(this.selectedDishWeight);
      objDish.pp = this.selectedDishPrice;

      this.selectedDish.push(objDish);

      this.orderDish = false;
    },
    submitDish() {
      let paramObj = Object.create(null);
      paramObj.availableKg = this.availableKg;
      paramObj.kgOrder = this.kgOrder;
      paramObj.selectedDish = this.selectedDish;

      this.$emit("submit-dish", paramObj);
    }
  }
};
</script>
<style scoped>
.fade-enter-active,
.fade-leave-active {
  transition: all 0.5s ease;
}
.fade-enter,
.fade-leave-active {
  opacity: 0;
}
.cell-img {
  width: 300px;
}
@media only screen and (min-width: 768px) {
  .cell-img {
    width: 150px;
  }
}
</style>
