<template>
  <div class="container mt-3" v-bind:style="styleObject">
    <b-breadcrumb :items="crumbs"></b-breadcrumb>
    <BilaoOrder
      v-if="isBilao"
      :itemOrder="itemOrder"
      @proceed-bilao="addToCart"
    />
    <CrabOrder
      v-if="isCrab"
      :itemOrder="itemOrder"
      :crabOrderDetails="crabOrderDetails"
      :item="product"
      @proceed-crab="addToCart"
    />
    <CrabOrder
      v-if="isItem"
      :itemOrder="itemOrder"
      :crabOrderDetails="itemOrderDetails"
      :item="product"
      @proceed-crab="addToCart"
    />
  </div>
</template>

<script>
import BilaoOrder from "@/components/BilaoOrder.vue";
import CrabOrder from "@/components/CrabOrder.vue";
import { BBreadcrumb } from "bootstrap-vue";

export default {
  name: "order-summary",
  props: ["styles", "products", "bilao", "offsetHt", "crabs", "items"],
  data: function() {
    return {
      style: this.$route.params.style,
      product: this.$route.params.product,
      productId: this.$route.params.id,
      styleObject: {
        minHeight: null
      },
      isBilao: false,
      isCrab: false,
      isItem: false,
      crabOrderDetails: null,
      itemsList: this.items.items,
      itemsName: this.items.itemsName,
      itemOrderDetails: null
    };
  },
  created: function() {
    //add Bilao
    let prodList = ["bilao"];
    this.products.forEach(p => {
      prodList.push(p.title.toLowerCase());
    });
    if (
      !(this.styles.indexOf(this.style) >= 0) ||
      !(prodList.indexOf(this.product.toLowerCase()) >= 0)
    ) {
      this.$router.replace({ name: "home" });
    }

    let check = null;
    switch (this.product.toLowerCase()) {
      case "bilao":
        check = this.checkExistProduct(this.bilao, this.productId);
        check = true;
        this.isBilao = true;
        break;
      case "crab":
        check = this.checkExistProduct(this.crabs, this.productId);
        this.isCrab = true;

        if (!sessionStorage.getItem("crabOrderDetails")) {
          this.$router.replace({ name: "home" });
        }

        this.crabOrderDetails = JSON.parse(
          sessionStorage.getItem("crabOrderDetails")
        );
        sessionStorage.clear();
        break;
      case "shrimp":
        check = true;
        if (!(this.itemsName.indexOf(this.product) >= 0)) {
          this.$router.replace({ name: "home" });
        }
        check = this.checkExistProduct(this.itemsFinalList, this.productId);

        if (!sessionStorage.getItem("itemOrderDetails")) {
          this.$router.replace({ name: "home" });
        }

        this.itemOrderDetails = JSON.parse(
          sessionStorage.getItem("itemOrderDetails")
        );
        sessionStorage.clear();

        this.isItem = true;
        break;
    }

    if (!check) this.$router.replace({ name: "home" });
  },
  mounted() {
    let headerHeight = document.querySelector("#pre-header").offsetHeight;
    let navHeight = document.querySelector("#main-menu").offsetHeight;
    let footerHeight = document.querySelector("#footer").offsetHeight;

    let ht =
      screen.height - (headerHeight + navHeight + footerHeight + this.offsetHt);

    this.styleObject.minHeight = ht + "px";

    
    document.querySelector("#pre-header").scrollIntoView({
      behavior: "smooth",
      alignToTop: true
    });
  },
  computed: {
    itemOrder() {
      let arrObj = null;
      switch (this.product.toLowerCase()) {
        case "bilao":
          arrObj = this.bilao;
          break;
        case "crab":
          arrObj = this.crabs;
          break;
        case "shrimp":
          arrObj = this.itemsFinalList;
          break;
      }

      return arrObj[arrObj.findIndex(el => el.id == this.productId)];
    },
    pageTitle() {
      return `Order Summary for ${this.itemOrder.title}`;
    },
    itemsFinalList() {
      return this.items.items[this.items.itemsName.indexOf(this.product)][
        this.product
      ];
    },
    crumbs() {
      let cArr = [{ text: "Home", to: { name: "home" } }];

      if (this.isBilao) {
        cArr.push({ text: "Bilao Order", active: true });
        return cArr;
      }

      if (this.style.toLowerCase() == "palengke") {
        cArr.push({ text: "Palengke", to: { name: "Palengke" } });
        if (this.product == "Crab") {
          cArr.push({
            text: this.product,
            to: {
              name: "Crab",
              params: { style: "palengke" }
            }
          });
        } else {
          cArr.push({
            text: this.product,
            to: {
              name: "Item",
              params: {
                item: this.product,
                style: "palengke"
              }
            }
          });
        }
      } else {
        cArr.push({ text: "Paluto", to: { name: "Paluto" } });
        if (this.product == "Crab") {
          cArr.push({
            text: this.product,
            to: {
              name: "Crab",
              params: { style: "paluto" }
            }
          });
        } else {
          cArr.push({
            text: this.product,
            to: {
              name: "Item",
              params: {
                item: this.product,
                style: "paluto"
              }
            }
          });
        }
      }

      if (this.product == "Crab") {
        //crab summary
        cArr.push({
          text: this.itemOrder.title + " Order",
          to: {
            name: "CrabSummary",
            params: {
              style: this.style,
              id: this.productId
            }
          }
        });
      } else {
        cArr.push({
          text: this.itemOrder.title + " Order",
          to: {
            name: "ItemSummary",
            params: {
              item: this.product,
              style: this.style,
              id: this.productId
            }
          }
        });
      }

      cArr.push({ text: this.itemOrder.title + " Details", active: true });

      return cArr;
    }
  },
  components: {
    BilaoOrder,
    CrabOrder,
    "b-breadcrumb": BBreadcrumb
  },
  methods: {
    checkExistProduct(list, productId) {
      return list.some(el => {
        return el.id == productId;
      });
    },
    addToCart(itemCart) {
      if (!localStorage.cart) {
        //init the local storage
        localStorage.cart = JSON.stringify(new Array());
      }
      let cart = JSON.parse(localStorage.cart);
      itemCart.cartNo = "cid-" + cart.length;
      cart.push(itemCart);
      localStorage.cart = JSON.stringify(cart);

      this.$emit("updateCart");

      this.$router.replace({ name: "Cart" });
    }
  }
};
</script>
