<template>
  <b-container class="mt-3" v-bind:style="styleObject">
    <b-breadcrumb :items="crumbs"></b-breadcrumb>
    <b-row>
      <b-col cols="12" sm="3">
        <img
          :src="require(`@/assets/images/${itemOrder.img}`)"
          class="w-75 rounded img-thumbnail d-block mx-auto"
        />
      </b-col>
      <b-col cols="12" sm="9">
        <h2>{{ itemOrder.title }}</h2>
        <h3>&#8369; {{ unitPrice }}</h3>
        <b-form-select
          v-model="gender"
          :options="genderList"
          v-if="showGender"
          class="mb-3"
          @change="updateFirstMaxtrix"
        ></b-form-select>
        <b-form-select
          v-model="size"
          :options="sizeList"
          v-if="showSize"
          @change="updateSecondMatrix"
          class="mb-3"
        ></b-form-select>
        <label for="kilo">Select kg weight below</label>
        <b-form-select
          v-model="weight"
          :options="weightKg"
          class="mb-3"
        ></b-form-select>
        <h3>&#8369; {{ marketPrice }}</h3>
        <b-button
          block
          class="mb-2"
          variant="outline-dark"
          @click.prevent="backToCrabs"
        >
          Back to crab list
        </b-button>
        <b-button
          block
          class="mb-2"
          variant="dark"
          @click.prevent="continueToOrder"
        >
          Continue to order
        </b-button>
      </b-col>
    </b-row>
  </b-container>
</template>
<script>
import {
  BContainer,
  BRow,
  BCol,
  BFormSelect,
  BButton,
  BBreadcrumb
} from "bootstrap-vue";
export default {
  name: "crabsummary",
  props: ["crabs", "styles", "weightKg", "offsetHt"],
  data: function() {
    return {
      style: this.$route.params.style,
      productId: this.$route.params.id,
      firstmatrix: "0",
      secondMatrix: "0",
      gender: "",
      size: "",
      weight: 0.5,
      styleObject: {
        minHeight: null
      }
    };
  },
  computed: {
    matrix: function() {
      return this.firstmatrix + this.secondMatrix;
    },
    itemOrder: function() {
      return this.crabs[this.crabs.map(e => e.id).indexOf(this.productId)];
    },
    unitPrice: function() {
      return this.itemOrder.price_maxtrix[
        this.itemOrder.price_maxtrix.map(e => e.idx).indexOf(this.matrix)
      ].price;
    },
    showGender() {
      return this.itemOrder.gender.length > 1;
    },
    genderList() {
      return this.itemOrder.gender;
    },
    showSize() {
      return this.itemOrder.size.length > 1;
    },
    sizeList() {
      return this.itemOrder.size;
    },
    marketPrice() {
      return this.weight * parseFloat(this.unitPrice);
    },
    crumbs() {
      let cArr = [{ text: "Home", to: { name: "home" } }];

      if (this.style.toLowerCase() == "palengke") {
        cArr.push({ text: "Palengke", to: { name: "Palengke" } });
        cArr.push({
          text: "Crab",
          to: { name: "Crab", params: { style: "palengke" } }
        });
      } else {
        cArr.push({ text: "Paluto", to: { name: "Paluto" } });
        cArr.push({
          text: "Crab",
          to: { name: "Crab", params: { style: "paluto" } }
        });
      }

      cArr.push({ text: this.itemOrder.title + " Order", active: true });

      return cArr;
    }
  },
  created: function() {
    //check if valid parameters
    let crabId = this.crabs.map(e => e.id);
    if (
      !(this.styles.indexOf(this.style) >= 0) ||
      !(crabId.indexOf(this.productId) >= 0)
    ) {
      this.$router.replace({ name: "home" });
    }

    this.gender = this.itemOrder.gender[0];
    this.size = this.sizeList[0];
  },
  mounted() {
    let headerHeight = document.querySelector("#pre-header").offsetHeight;
    let navHeight = document.querySelector("#main-menu").offsetHeight;
    let footerHeight = document.querySelector("#footer").offsetHeight;

    let ht =
      screen.height - (headerHeight + navHeight + footerHeight + this.offsetHt);

    this.styleObject.minHeight = ht + "px";
  },
  components: {
    "b-container": BContainer,
    "b-row": BRow,
    "b-col": BCol,
    "b-form-select": BFormSelect,
    "b-button": BButton,
    "b-breadcrumb": BBreadcrumb
  },
  methods: {
    updateFirstMaxtrix() {
      this.firstmatrix = this.genderList.indexOf(this.gender).toString();
    },
    updateSecondMatrix() {
      this.secondMatrix = this.sizeList.indexOf(this.size).toString();
    },
    backToCrabs() {
      this.$router.replace({ name: "Crab", params: { style: this.style } });
    },
    prepareSession() {
      //use session storage since info will only be passed to next page
      //clear first the session storage
      sessionStorage.clear();

      //get product details
      let prod_details = "";
      if (this.gender != "") prod_details = this.gender;
      if (this.size != "")
        prod_details +=
          prod_details == "" ? `${this.size} size` : `/ ${this.size} size`;

      let crabOrderDetails = Object.create(null);
      crabOrderDetails.prod_details = prod_details;
      crabOrderDetails.unitPrice = this.unitPrice;
      crabOrderDetails.weight = this.weight;
      crabOrderDetails.marketPrice = this.marketPrice;

      sessionStorage.crabOrderDetails = JSON.stringify(crabOrderDetails);
    },
    contiueToOrderSummaryPalengke() {
      this.prepareSession();

      this.$router.replace({
        name: "OrderSummary",
        params: { style: this.style, product: "Crab", id: this.productId }
      });
    },
    continueToDish() {
      //get product details
      let prod_details = "";
      if (this.gender != "") prod_details = this.gender;
      if (this.size != "")
        prod_details +=
          prod_details == "" ? `${this.size} size` : `/ ${this.size} size`;

      let crabOrderDetails = Object.create(null);
      crabOrderDetails.prod_details = prod_details;
      crabOrderDetails.unitPrice = this.unitPrice;
      crabOrderDetails.weight = this.weight;
      crabOrderDetails.marketPrice = this.marketPrice;

      this.$router.replace({
        name: "DishRestaurantSummary",
        params: {
          style: this.style,
          item: "Crab",
          id: this.productId,
          orderDetails: JSON.stringify(crabOrderDetails)
        }
      });
    },
    continueToOrder() {
      if (this.style == "palengke") {
        this.contiueToOrderSummaryPalengke();
      } else {
        //Paluto
        this.continueToDish();
      }
    }
  }
};
</script>
