<template>
  <b-container>
    <b-row>
      <b-col cols="12" sm="3">
        <img
          :src="require(`@/assets/images/${itmOrder.img}`)"
          class="w-75 rounded img-thumbnail d-block mx-auto"
        />
      </b-col>
      <b-col cols="12" sm="9" class="h-100">
        <h2 id="selectedResto" class="d-block w-100">
          Restaurant select for {{ itmOrder.title }}
        </h2>
        <h3>Selected restaurant: {{ selectedRestaurant }}</h3>
        <h3 class="d-block w-100">
          <span
            class="d-inline-block mr-3"
            v-for="(mode, index) in displayModes"
            :key="index"
          >
            <a
              href="#"
              class="text-secondary"
              :class="[{ 'text-dark': currentMode == mode.display }]"
              @click.prevent="switchMode(index)"
            >
              <font-awesome-icon :icon="mode.icon"></font-awesome-icon>
            </a>
          </span>
        </h3>
        <transition name="fade">
          <div v-show="isGrid">
            <ProductsGrid
              :items="restaurants"
              label="Select"
              @next-step="proceedNext"
            />
          </div>
        </transition>
        <transition name="fade">
          <div v-show="isList">
            <ProductsList
              :items="restaurants"
              label="Select"
              @next-step="proceedNext"
            />
          </div>
        </transition>
        <b-button
          block
          class="mb-2"
          variant="dark"
          :disabled="restoIsSelected"
          @click="proceedSummary"
        >
          Continue to order
        </b-button>
        <b-table-simple stacked="lg">
          <b-thead>
            <b-tr>
              <b-th>#</b-th>
              <b-th>Pic</b-th>
              <b-th>Dish</b-th>
              <b-th>Kg Weight</b-th>
              <b-th>Special Instruction</b-th>
              <b-th>Market Price</b-th>
              <b-th>Paluto Price</b-th>
            </b-tr>
          </b-thead>
          <b-tbody>
            <b-tr v-for="dsh in dishes" :key="dsh.id">
              <b-td stacked-heading="#">{{ dsh.id }}</b-td>
              <b-td class="p-0 cell-img">
                <img
                  :src="require(`@/assets/images/${dsh.img}`)"
                  class="w-100 rounded img-thumbnail d-inline"
                />
                <slot></slot>
              </b-td>
              <b-td stacked-heading="Dish">{{ dsh.dish }}</b-td>
              <b-td stacked-heading="Kg Weight">{{ dsh.kg }}</b-td>
              <b-td stacked-heading="Special Instruction">{{ dsh.si }}</b-td>
              <b-td stacked-heading="Market Price">{{ dsh.mp }}</b-td>
              <b-td stacked-heading="Paluto Price">{{ dsh.pp }}</b-td>
            </b-tr>
          </b-tbody>
        </b-table-simple>
      </b-col>
    </b-row>
  </b-container>
</template>
<script>
import {
  BContainer,
  BRow,
  BCol,
  BButton,
  BTableSimple,
  BTr,
  BTh,
  BTd,
  BThead,
  BTbody
} from "bootstrap-vue";
import ProductsGrid from "@/components/ProductsGrid.vue";
import ProductsList from "@/components/ProductsList.vue";
import { FontAwesomeIcon } from "@fortawesome/vue-fontawesome";

export default {
  name: "resturantselect",
  props: ["itmOrder", "selectedDish", "restaurants"],
  data: function() {
    return {
      selectedRestaurant: null,
      selectedRestaurantImg: null,
      currentMode: "list",
      displayModes: [
        { display: "list", icon: "th-list" },
        { display: "grid", icon: "th" }
      ]
    };
  },
  mounted() {
    document.querySelector("#pre-header").scrollIntoView({
      behavior: "smooth",
      alignToTop: true
    });
  },
  computed: {
    isGrid() {
      return this.currentMode == "grid";
    },
    isList() {
      return this.currentMode == "list";
    },
    restoIsSelected() {
      return !this.selectedRestaurant;
    },
    dishes() {
      return JSON.parse(this.selectedDish);
    }
  },
  components: {
    "b-container": BContainer,
    "b-row": BRow,
    "b-col": BCol,
    "b-button": BButton,
    "b-table-simple": BTableSimple,
    "b-tr": BTr,
    "b-th": BTh,
    "b-td": BTd,
    "b-thead": BThead,
    "b-tbody": BTbody,
    ProductsGrid,
    ProductsList,
    FontAwesomeIcon
  },
  methods: {
    switchMode(index) {
      this.currentMode = this.displayModes[index].display;
    },
    proceedNext(orderParams) {
      this.selectedRestaurant = orderParams.title;
      this.selectedRestaurantImg = orderParams.img;

      document.querySelector("#pre-header").scrollIntoView({
        behavior: "smooth",
        alignToTop: true
      });
    },
    proceedSummary() {
      let paramObj = Object.create(null);

      paramObj.title = this.selectedRestaurant;
      paramObj.img = this.selectedRestaurantImg;

      this.$emit("proceed-summary", paramObj);
    }
  }
};
</script>
<style scoped>
.fade-enter-active,
.fade-leave-active {
  transition: all 0.5s ease;
}
.fade-enter,
.fade-leave-active {
  opacity: 0;
}
.cell-img {
  width: 300px;
}
@media only screen and (min-width: 768px) {
  .cell-img {
    width: 150px;
  }
}
</style>
