<template>
  <div class="container mt-3">
    <b-breadcrumb :items="crumbs"></b-breadcrumb>
    <h2>Crab</h2>
    <h3>
      <span
        class="d-inline-block mr-3"
        v-for="(mode, index) in displayModes"
        :key="index"
      >
        <a
          href="#"
          class="text-secondary"
          :class="[{ 'text-dark': currentMode == mode.display }]"
          @click.prevent="switchMode(index)"
        >
          <font-awesome-icon :icon="mode.icon"></font-awesome-icon>
        </a>
      </span>
    </h3>
    <transition name="fadeD">
      <div v-show="isGrid">
        <ProductsGrid :items="crabs" label="Order" @next-step="proceedNext" />
      </div>
    </transition>
    <transition name="fadeD">
      <div v-show="isList">
        <ProductsList :items="crabs" label="Order" @next-step="proceedNext" />
      </div>
    </transition>
  </div>
</template>
<script>
import ProductsGrid from "@/components/ProductsGrid.vue";
import ProductsList from "@/components/ProductsList.vue";
import { BBreadcrumb } from "bootstrap-vue";
import { FontAwesomeIcon } from "@fortawesome/vue-fontawesome";

export default {
  name: "palengke",
  props: ["crabs", "styles"],
  components: {
    ProductsGrid,
    ProductsList,
    "b-breadcrumb": BBreadcrumb,
    FontAwesomeIcon
  },
  data: function() {
    return {
      displayModes: [
        { display: "list", icon: "th-list" },
        { display: "grid", icon: "th" }
      ],
      currentMode: "list",
      style: this.$route.params.style
    };
  },
  created: function() {
    if (!(this.styles.indexOf(this.style) >= 0)) {
      this.$router.replace({ name: "home" });
    }
  },
  computed: {
    isGrid() {
      return this.currentMode == "grid";
    },
    isList() {
      return this.currentMode == "list";
    },
    crumbs() {
      let cArr = [{ text: "Home", to: { name: "home" } }];

      if (this.style.toLowerCase() == "palengke") {
        cArr.push({ text: "Palengke", to: { name: "Palengke" } });
      } else {
        cArr.push({ text: "Paluto", to: { name: "Paluto" } });
      }

      cArr.push({ text: "Crab", active: true });

      return cArr;
    }
  },
  methods: {
    switchMode(index) {
      this.currentMode = this.displayModes[index].display;
    },
    proceedNext(orderParams) {
      this.$router.push({
        name: "CrabSummary",
        params: { style: this.style, id: orderParams.id }
      });
    }
  }
};
</script>
<style scoped>
.fadeD-enter-active,
.fadeD-leave-active {
  transition: all 0.5s ease;
}
.fadeD-enter,
.fadeD-leave-active {
  opacity: 0;
}
</style>
