<template>
  <b-container>
    <b-row>
      <b-col cols="12" sm="3">
        <img
          :src="require(`@/assets/images/${itemOrder.img}`)"
          class="w-75 rounded img-thumbnail d-block mx-auto"
        />
      </b-col>
      <b-col cols="12" sm="9">
        <h2>{{ itemOrder.title }}</h2>
        <p>{{ itemOrder.desc }}</p>
        <h3>&#8369; {{ itemOrder.prize }}</h3>
        <b-form-group label="Pick up or Pa Deliver">
          <b-form-radio v-model="acquire" name="acquire" value="pick-up">
            Pick Up
          </b-form-radio>
          <b-form-radio v-model="acquire" name="acquire" value="deliver">
            Deliver
          </b-form-radio>
        </b-form-group>
        <h3>{{ enterInfo }}</h3>
        <p><small>All entries below are required.</small></p>
        <b-alert show variant="warning" v-if="errors.length">
          <p>Please correct the following errors</p>
          <b-list-group>
            <b-list-group-item v-for="(error, index) in errors" :key="index">
              {{ error }}
            </b-list-group-item>
          </b-list-group>
        </b-alert>
        <b-form-group id="Customer-Group" label="Name" label-for="customerName">
          <b-form-input
            id="customerName"
            v-model="customerName"
            required
            placeholder="Enter your name"
          ></b-form-input>
        </b-form-group>
        <b-form-group
          id="ContactNo"
          label="Contact No"
          label-for="contactNumber"
        >
          <b-form-input
            id="contactNumber"
            v-model="contactNumber"
            placeholder="Enter your contact number"
          ></b-form-input>
        </b-form-group>
        <b-form-group
          id="acquireDateGrp"
          :label="acquireDateLabel"
          label-for="acquireDate"
        >
          <b-form-input
            id="acquireDate"
            v-model="acquireDate"
            type="date"
          ></b-form-input>
        </b-form-group>
        <b-form-group
          id="acquireTimeGrp"
          :label="acquireTimeLabel"
          label-for="acquireTime"
        >
          <b-form-input
            id="acquireTime"
            v-model="acquireTime"
            type="time"
          ></b-form-input>
        </b-form-group>
        <div>
          <b-button block class="mb-2" variant="outline-dark" to="/palengke">
            Visit our palengke
          </b-button>
          <b-button block class="mb-2" variant="outline-dark" to="/paluto">
            Choose from our paluto
          </b-button>
          <b-button block class="mb-5" variant="dark" @click="prepareBilao">
            Add to cart <font-awesome-icon icon="cart-plus"></font-awesome-icon>
          </b-button>
        </div>
      </b-col>
    </b-row>
  </b-container>
</template>
<script>
import {
  BContainer,
  BRow,
  BCol,
  BFormGroup,
  BFormRadio,
  BFormInput,
  BButton,
  BAlert,
  BListGroup,
  BListGroupItem
} from "bootstrap-vue";
import { FontAwesomeIcon } from "@fortawesome/vue-fontawesome";

export default {
  name: "bilaoorder",
  props: ["itemOrder"],
  data: function() {
    return {
      acquire: "pick-up",
      customerName: null,
      contactNumber: null,
      acquireDate: null,
      acquireTime: null,
      errors: []
    };
  },
  components: {
    "b-container": BContainer,
    "b-row": BRow,
    "b-col": BCol,
    "b-form-group": BFormGroup,
    "b-form-radio": BFormRadio,
    "b-form-input": BFormInput,
    "b-button": BButton,
    FontAwesomeIcon,
    "b-alert": BAlert,
    "b-list-group": BListGroup,
    "b-list-group-item": BListGroupItem
  },
  computed: {
    enterInfo() {
      if (this.acquire == "pick-up") return "Enter Pick Up Info";
      else return "Enter Deliver Info";
    },
    acquireDateLabel() {
      if (this.acquire == "pick-up") return "Enter Pick Up Date";
      else return "Enter Delivery Date";
    },
    acquireTimeLabel() {
      if (this.acquire == "pick-up") return "Enter Pick Up Time";
      else return "Enter Delivery Time";
    }
  },
  methods: {
    prepareBilao() {
      this.errors = [];
      if (
        this.customerName &&
        this.contactNumber &&
        this.acquireDate &&
        this.acquireTime
      ) {
        let bilaoCart = {
          style: "palengke",
          item_id: this.itemOrder.id,
          item: "Bilao",
          subitem: this.itemOrder.title,
          prod_details: this.itemOrder.desc,
          weight: "",
          market_price: this.itemOrder.prize,
          img: this.itemOrder.img,
          restaurant: "",
          paluto: "Bilao",
          mode: this.acquire,
          details: {
            name: this.customerName,
            contact: this.contactNumber,
            date: this.acquireDate,
            time: this.acquireTime
          },
          paluto_price: ""
        };
        this.$emit("proceed-bilao", bilaoCart);
      } else {
        if (!this.customerName) this.errors.push("Name is required");
        if (!this.contactNumber) this.errors.push("Contact number is required");
        if (!this.acquireDate) this.errors.push("Date is required");
        if (!this.acquireTime) this.errors.push("Time is required");
      }
    }
  }
};
</script>
