<template>
  <b-carousel
    id="multi-slide"
    controls
    indicators
    :interval="0"
    background="#fff"
    class="carousel-inner"
    :img-width="imgW"
    :img-height="imgH"
  >
    <b-carousel-slide
      img-blank
      img-alt="products"
      v-for="item in items"
      :key="item.id"
    >
      <div class="col-sm-12">
        <section class="card h-100 border-dark">
          <img
            class="card-img-top"
            :src="require(`@/assets/images/${item.img}`)"
          />
          <div class="card-body">
            <h4 class="card-title text-dark">{{ item.title }}</h4>
            <h5 class="text-dark">&#8369; {{ item.prize }}</h5>
            <p class="text-dark">{{ item.desc }}</p>
          </div>
          <div class="card-footer">
            <a
              href="#"
              class="card-link"
              @click.prevent="placeBilaoOrder(item.id)"
            >
              {{ label }}
            </a>
          </div>
        </section>
      </div>
    </b-carousel-slide>
  </b-carousel>
</template>

<script>
import { BCarousel, BCarouselSlide } from "bootstrap-vue";

export default {
  name: "multicardcarousel",
  props: ["items", "imgW", "imgH", "label"],
  components: {
    "b-carousel": BCarousel,
    "b-carousel-slide": BCarouselSlide
  },
  methods: {
    placeBilaoOrder(id) {
      this.$emit("place-bilao-order", id);
    }
  }
};
</script>

<style>
.carousel-control-prev-icon {
  background-image: url("data:image/svg+xml;charset=utf8,%3Csvg xmlns='http://www.w3.org/2000/svg' fill='%23000' viewBox='0 0 8 8'%3E%3Cpath d='M5.25 0l-4 4 4 4 1.5-1.5-2.5-2.5 2.5-2.5-1.5-1.5z'/%3E%3C/svg%3E");
}

.carousel-control-next-icon {
  background-image: url("data:image/svg+xml;charset=utf8,%3Csvg xmlns='http://www.w3.org/2000/svg' fill='%23000' viewBox='0 0 8 8'%3E%3Cpath d='M1.5 0l-1.5 1.5 2.5 2.5-2.5 2.5 1.5 1.5 4-4-4-4z'/%3E%3C/svg%3E");
}
</style>
