<template>
  <div id="main">
    <div id="header" class="container-fluid d-none d-sm-block shadow-sm">
      <div class="row">
        <div class="col-sm-12 " style="height: 230px">
          <img
            src="../assets/images/logo.jpg"
            class="rounded mx-auto d-block mt-5"
          />
        </div>
      </div>
    </div>

    <div class="container mt-3 mt-sm-5">
      <!--3 columns:start-->
      <div class="row">
        <div class="col-sm-12 col-md-4 mb-3">
          <section class="card h-100 border-dark" id="palengke">
            <img
              class="card-img-top"
              src="../assets/images/home/palengke.png"
            />
            <div class="card-body">
              <h2 class="card-title">Palengke</h2>
              <p>
                {{ market }}
              </p>
            </div>
            <div class="card-footer">
              <router-link to="/palengke">
                Visit our palengke
              </router-link>
            </div>
          </section>
        </div>

        <div class="col-sm-12 col-md-4 mb-3">
          <section class="card h-100 border-dark" id="paluto">
            <img class="card-img-top" src="../assets/images/home/paluto.png" />
            <div class="card-body">
              <h2 class="card-title">Paluto</h2>
              <p>
                {{ cooking }}
              </p>
            </div>
            <div class="card-footer">
              <router-link to="/paluto">
                Choose your paluto
              </router-link>
            </div>
          </section>
        </div>

        <div class="col-sm-12 col-md-4 mb-3">
          <section class="card h-100 border-dark" id="restaurant">
            <img
              class="card-img-top"
              src="../assets/images/home/restaurant.png"
            />
            <div class="card-body">
              <h2 class="card-title">Restaurants</h2>
              <b-carousel id="resto-slide" controls indicators :interval="3000">
                <div>
                  <b-carousel-slide
                    v-for="item in restaurants"
                    :key="item.id"
                    :img-src="require(`@/assets/images/${item.img}`)"
                  ></b-carousel-slide>
                </div>
              </b-carousel>
            </div>
            <div class="card-footer">
              <a href="#" class="card-link">Reserve at our restaurants</a>
            </div>
          </section>
        </div>
      </div>
    </div>
    <!--3 columns:end-->
    <div class="container">
      <!--bilao: start-->
      <h2>Our Bilao Specials</h2>
      <h3>
        <span
          class="d-inline-block mr-3"
          v-for="(mode, index) in displayModes"
          :key="index"
        >
          <a
            href="#"
            class="text-secondary"
            :class="[{ 'text-dark': currentMode == mode.display }]"
            @click.prevent="switchMode(index)"
          >
            <font-awesome-icon :icon="mode.icon"></font-awesome-icon>
          </a>
        </span>
      </h3>
      <transition name="fadeD">
        <div v-show="isCarousel">
          <MultiCardCarousel
            class="px-0 d-none d-sm-block"
            :cardsPerSlide="cardsPerSlide"
            :items="bilao"
            @place-bilao-order="bilaoOrder"
          />
        </div>
      </transition>
      <transition name="fadeD">
        <div v-show="isCarousel">
          <SingleCardCarousel
            class="container d-block d-sm-none"
            :items="bilao"
            :imgW="1000"
            :imgH="1400"
            :label="passLabel"
            @place-bilao-order="bilaoOrder"
          />
        </div>
      </transition>
      <transition name="fadeD">
        <div v-show="isGrid">
          <BilaoGrid
            class="container"
            :items="bilao"
            @place-bilao-order="bilaoOrder"
          />
        </div>
      </transition>
      <transition name="fadeD">
        <div v-show="isList">
          <BilaoList
            class="container"
            :items="bilao"
            @place-bilao-order="bilaoOrder"
          />
        </div>
      </transition>
    </div>
  </div>
  <!--#main-->
</template>
<script>
// @ is an alias to /src
//import HelloWorld from "@/components/HelloWorld.vue";
import { BCarousel } from "bootstrap-vue";
import { BCarouselSlide } from "bootstrap-vue";
import MultiCardCarousel from "@/components/MultiCardCarousel.vue";
import SingleCardCarousel from "@/components/SingleCardCarousel.vue";
import BilaoGrid from "@/components/BilaoGrid.vue";
import BilaoList from "@/components/BilaoList.vue";
import { FontAwesomeIcon } from "@fortawesome/vue-fontawesome";

export default {
  name: "home",
  props: ["bilao", "restaurants"],
  data: function() {
    return {
      market:
        "Drop our palengke and order from our excellent choice of fresh seafoods and more",
      cooking:
        "Choose from our palengke products and select from our menu to prepare your meal",
      cardsPerSlide: 2,
      displayModes: [
        { display: "carousel", icon: "sliders-h" },
        { display: "list", icon: "th-list" },
        { display: "grid", icon: "th" }
      ],
      currentMode: "carousel",
      passLabel: "Order Now"
    };
  },
  computed: {
    isCarousel() {
      return this.currentMode == "carousel";
    },
    isGrid() {
      return this.currentMode == "grid";
    },
    isList() {
      return this.currentMode == "list";
    }
  },
  components: {
    "b-carousel": BCarousel,
    "b-carousel-slide": BCarouselSlide,
    MultiCardCarousel,
    SingleCardCarousel,
    BilaoGrid,
    BilaoList,
    FontAwesomeIcon
  },
  methods: {
    bilaoOrder(id) {
      this.$router.replace({
        name: "OrderSummary",
        params: { style: "palengke", product: "Bilao", id: id }
      });
    },
    switchMode(index) {
      this.currentMode = this.displayModes[index].display;
    }
  }
};
</script>

<style scoped>
#header {
  background-image: url("../assets/images/desktop-banner-v4.png");
}
.fadeD-enter-active,
.fadeD-leave-active {
  transition: all 0.5s ease;
}
.fadeD-enter,
.fadeD-leave-active {
  opacity: 0;
}
</style>
