<template>
  <div>
    <b-card no-body class="overflow-hidden" v-for="item in items" :key="item.id" style="max-width:550px; margin-bottom:10px">
      <b-row no-gutters>
        <b-col>
          <b-card-img :src="require(`@/assets/images/${item.img}`)" class="rounded-0"></b-card-img>
        </b-col>
        <b-col>
          <b-card-body :title="item.title">
            <b-card-text>
             <p>{{item.desc}} </p>
            </b-card-text>
            <b-card-footer>
              <a
                href="#"
                class="card-link"
                @click.prevent="placeBilaoOrder(item.id)"
              >
	              Order Now
              </a>
            </b-card-footer>
          </b-card-body>
        </b-col>
      </b-row>
    </b-card>
  </div>
</template>

<script>
import { BCard, BRow, BCol, BCardImg, BCardBody, BCardText, BCardFooter } from "bootstrap-vue";

export default {
  name: "bilaolist",
  props: ["items"],
  components: {
    "b-card": BCard,
    "b-row": BRow,
    "b-col": BCol,
    "b-card-img": BCardImg,
    "b-card-body": BCardBody,
    "b-card-text": BCardText,
    "b-card-footer": BCardFooter
  },
  methods: {
    placeBilaoOrder(id) {
      this.$emit("place-bilao-order", id);
    }
  }
}
</script>