<template>
  <b-carousel
    id="multi-slide"
    controls
    indicators
    :interval="0"
    background="#fff"
    img-width="1000"
    img-height="580px"
    class="carousel-inner"
  >
    <div v-for="(indArr, index) in groupedItems" :key="index">
      <b-carousel-slide img-blank img-alt="products">
        <div class="container-fluid">
          <div class="row">
            <div class="col-sm-6" v-for="item in indArr" :key="item.id">
              <section class="card h-100 border-dark">
                <img
                  class="card-img-top"
                  :src="require(`@/assets/images/${item.img}`)"
                />
                <div class="card-body">
                  <h4 class="card-title text-dark">{{ item.title }}</h4>
                  <h5 class="text-dark">&#8369; {{ item.prize }}</h5>
                  <p class="text-dark">{{ item.desc }}</p>
                </div>
                <div class="card-footer">
                  <a
                    href="#"
                    class="card-link"
                    @click.prevent="placeBilaoOrder(item.id)"
                  >
                    Order Now
                  </a>
                </div>
              </section>
            </div>
          </div>
        </div>
      </b-carousel-slide>
    </div>
  </b-carousel>
</template>

<script>
//intended for Bilao on home page
import { BCarousel, BCarouselSlide } from "bootstrap-vue";

export default {
  name: "multicardcarousel",
  props: ["cardsPerSlide", "items"],
  components: {
    "b-carousel": BCarousel,
    "b-carousel-slide": BCarouselSlide
  },
  computed: {
    groupedItems: function() {
      let groupArray = [];
      let indGroup = [];
      this.items.forEach((itm, idx) => {
        indGroup.push(itm);
        if (indGroup.length == this.cardsPerSlide) {
          groupArray.push(indGroup);
          indGroup = [];
        } else if (idx == this.items.length - 1) {
          groupArray.push(indGroup);
        }
      });

      return groupArray;
    }
  },
  methods: {
    placeBilaoOrder(id) {
      this.$emit("place-bilao-order", id);
    }
  }
};
</script>
<style>
.carousel-control-prev-icon {
  background-image: url("data:image/svg+xml;charset=utf8,%3Csvg xmlns='http://www.w3.org/2000/svg' fill='%23000' viewBox='0 0 8 8'%3E%3Cpath d='M5.25 0l-4 4 4 4 1.5-1.5-2.5-2.5 2.5-2.5-1.5-1.5z'/%3E%3C/svg%3E");
}

.carousel-control-next-icon {
  background-image: url("data:image/svg+xml;charset=utf8,%3Csvg xmlns='http://www.w3.org/2000/svg' fill='%23000' viewBox='0 0 8 8'%3E%3Cpath d='M1.5 0l-1.5 1.5 2.5 2.5-2.5 2.5 1.5 1.5 4-4-4-4z'/%3E%3C/svg%3E");
}

#multi-slide .carousel-caption {
  top: 10px;
  padding-top: 0;
  bottom: 0px;
}
</style>
