<template>
  <b-container class="mt-3" v-bind:style="styleObject">
    <b-breadcrumb :items="crumbs"></b-breadcrumb>
    <b-row>
      <b-col cols="12" sm="3">
        <img
          :src="require(`@/assets/images/${itemOrder.img}`)"
          class="w-75 rounded img-thumbnail d-block mx-auto"
        />
      </b-col>
      <b-col cols="12" sm="9">
        <h2>{{ itemOrder.title }}</h2>
        <h3>&#8369; {{ unitPrice }}</h3>
        <b-form-select
          v-model="size"
          :options="sizeList"
          class="mb-3"
        ></b-form-select>
        <label for="kilo">Select kg weight below</label>
        <b-form-select
          v-model="weight"
          :options="weightKg"
          class="mb-3"
        ></b-form-select>
        <h3>&#8369; {{ marketPrice }}</h3>
        <b-button
          block
          class="mb-2"
          variant="outline-dark"
          @click.prevent="backToPrev"
        >
          Back to {{ this.item.toLowerCase() }} list
        </b-button>
        <b-button
          block
          class="mb-2"
          variant="dark"
          @click.prevent="continueToOrder"
        >
          Continue to order
        </b-button>
      </b-col>
    </b-row>
  </b-container>
</template>
<script>
import {
  BContainer,
  BRow,
  BCol,
  BFormSelect,
  BButton,
  BBreadcrumb
} from "bootstrap-vue";

export default {
  name: "itemsummary",
  props: ["items", "styles", "weightKg", "offsetHt"],
  data: function() {
    return {
      style: this.$route.params.style,
      productId: this.$route.params.id,
      item: this.$route.params.item,
      size: "",
      weight: 0.5,
      styleObject: {
        minHeight: null
      }
    };
  },
  mounted() {
    if (!(this.styles.indexOf(this.style) >= 0)) {
      this.$router.replace({ name: "home" });
    }

    if (typeof this.itemOrder == "undefined") {
      this.$router.replace({ name: "home" });
    }

    let headerHeight = document.querySelector("#pre-header").offsetHeight;
    let navHeight = document.querySelector("#main-menu").offsetHeight;
    let footerHeight = document.querySelector("#footer").offsetHeight;

    let ht =
      screen.height - (headerHeight + navHeight + footerHeight + this.offsetHt);

    this.styleObject.minHeight = ht + "px";

    this.size = this.sizeList[0];
  },
  components: {
    "b-container": BContainer,
    "b-row": BRow,
    "b-col": BCol,
    "b-form-select": BFormSelect,
    "b-button": BButton,
    "b-breadcrumb": BBreadcrumb
  },
  computed: {
    itemsList() {
      return this.items.items[this.items.itemsName.indexOf(this.item)][
        this.item
      ];
    },
    itemOrder() {
      return this.itemsList[
        this.itemsList.map(el => el.id).indexOf(this.productId)
      ];
    },
    sizeList() {
      return this.itemOrder.size;
    },
    unitPrice() {
      return this.itemOrder.price_matrix[this.sizeList.indexOf(this.size)];
    },
    marketPrice() {
      return this.weight * parseFloat(this.unitPrice);
    },
    crumbs() {
      let cArr = [{ text: "Home", to: { name: "home" } }];

      if (this.style.toLowerCase() == "palengke") {
        cArr.push({ text: "Palengke", to: { name: "Palengke" } });
        cArr.push({
          text: this.item,
          to: { name: "Item", params: { style: "palengke" } }
        });
      } else {
        cArr.push({ text: "Paluto", to: { name: "Paluto" } });
        cArr.push({
          text: this.item,
          to: { name: "Item", params: { style: "paluto" } }
        });
      }

      cArr.push({ text: this.itemOrder.title + " Order", active: true });

      return cArr;
    }
  },
  methods: {
    backToPrev() {
      this.$router.replace({
        name: "Item",
        params: { item: this.item, style: this.style }
      });
    },
    prepareSession() {
      //use session storage since info will only be passed to next page
      //clear first the session storage
      sessionStorage.clear();

      //get product details
      let prod_details = `${this.size} size`;

      let itemOrderDetails = Object.create(null);
      itemOrderDetails.prod_details = prod_details;
      itemOrderDetails.unitPrice = this.unitPrice;
      itemOrderDetails.weight = this.weight;
      itemOrderDetails.marketPrice = this.marketPrice;

      sessionStorage.itemOrderDetails = JSON.stringify(itemOrderDetails);
    },
    contiueToOrderSummaryPalengke() {
      this.prepareSession();

      this.$router.replace({
        name: "OrderSummary",
        params: { style: this.style, product: this.item, id: this.productId }
      });
    },
    continueToDish() {
      let prod_details = `${this.size} size`;

      let itemOrderDetails = Object.create(null);
      itemOrderDetails.prod_details = prod_details;
      itemOrderDetails.unitPrice = this.unitPrice;
      itemOrderDetails.weight = this.weight;
      itemOrderDetails.marketPrice = this.marketPrice;

      this.$router.replace({
        name: "DishRestaurantSummary",
        params: {
          style: this.style,
          item: this.item,
          id: this.productId,
          orderDetails: JSON.stringify(itemOrderDetails)
        }
      });
    },
    continueToOrder() {
      if (this.style == "palengke") {
        this.contiueToOrderSummaryPalengke();
      } else {
        //Paluto
        this.continueToDish();
      }
    }
  }
};
</script>
