<template>
  <div class="container">
    <div class="row">
      <div class="col-sm-3 mb-3" v-for="item in items" :key="item.id">
        <section class="card h-100 border-dark">
          <img
            class="card-img-top"
            :src="require(`@/assets/images/${item.img}`)"
          />
          <div class="card-body">
            <h4 class="card-title text-dark">{{ item.title }}</h4>
            <h5 class="text-dark">&#8369; {{ item.prize }}</h5>
            <p class="text-dark">{{ item.desc }}</p>
          </div>
          <div class="card-footer">
            <a
              href="#"
              class="card-link"
              @click.prevent="placeBilaoOrder(item.id)"
            >
              Order Now
            </a>
          </div>
        </section>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "bilaogrid",
  props: ["items"],
  methods: {
    placeBilaoOrder(id) {
      this.$emit("place-bilao-order", id);
    }
  }
};
</script>
