<template>
  <div>
    <div v-for="(indArr, index) in groupedItems" :key="index" class="mb-3">
      <b-card-group deck>
        <b-card
          v-for="item in indArr"
          :key="item.id"
          :title="item.title"
          :img-src="require(`@/assets/images/${item.img}`)"
          img-top
        >
          <b-card-footer class="px-0">
            <a
              href="#"
              class="card-link"
              @click.prevent="proceedOrder(item.id, item.title, item.img)"
            >
              {{ label }} {{ item.title }}
            </a>
          </b-card-footer>
        </b-card>
      </b-card-group>
    </div>
  </div>
</template>

<script>
import { BCardGroup, BCard, BCardFooter } from "bootstrap-vue";
export default {
  name: "productsgrid",
  props: ["items", "label"],
  data: function() {
    return {
      cardsPerGroup: 3
    };
  },
  components: {
    "b-card-group": BCardGroup,
    "b-card": BCard,
    "b-card-footer": BCardFooter
  },
  computed: {
    groupedItems: function() {
      let groupArray = [];
      let indGroup = [];
      this.items.forEach((itm, idx) => {
        indGroup.push(itm);
        if (indGroup.length == this.cardsPerGroup) {
          groupArray.push(indGroup);
          indGroup = [];
        } else if (idx == this.items.length - 1) {
          groupArray.push(indGroup);
        }
      });

      return groupArray;
    }
  },
  methods: {
    proceedOrder(id, title, img) {
      let orderParams = { id: id, title: title, img: img };
      this.$emit("next-step", orderParams);
    }
  }
};
</script>
