<template>
  <b-container class="mt-3" v-bind:style="styleObject">
    <b-row>
      <b-col cols="2" sm="1" class="font-weight-bold">Product</b-col>
      <b-col cols="5" sm="2" class="font-weight-bold">Paluto/ Padeliver</b-col>
      <b-col cols="5" sm="1" class="font-weight-bold">Weight</b-col>
      <b-col cols="4" sm="2" class="font-weight-bold">Total Market</b-col>
      <b-col cols="4" sm="2" class="font-weight-bold">Total Paluto</b-col>
      <b-col cols="4" sm="2" class="font-weight-bold">
        Total Deliver/ Restaurant
      </b-col>
      <b-col cols="12" sm="2">&nbsp;</b-col>
    </b-row>
    <b-row v-for="(item, index) in cartItems" :key="index">
      <b-col cols="2" sm="1">
        <img
          :src="require(`@/assets/images/${item.img}`)"
          class="w-100 rounded img-thumbnail"
        />
        <small>{{ item.subitem }}</small>
      </b-col>
      <b-col cols="5" sm="2">
        {{ item.subitem }} <br />
        {{ item.mode }}
      </b-col>
      <b-col cols="5" sm="1">{{ col3Weight(item.weight) }}</b-col>
      <b-col cols="4" sm="2">&#8369; {{ item.market_price }}</b-col>
      <b-col cols="4" sm="2">&#8369; {{ item.paluto_price }}</b-col>
      <b-col cols="4" sm="2">&#8369; {{ addPrices(item.market_price, item.paluto_price) }}</b-col>
      <b-col cols="12" sm="2">
        <b-button-group>
          <b-button :id="item.cartNo" variant="outline-dark">
            <font-awesome-icon icon="info-circle"></font-awesome-icon>
          </b-button>
          <b-popover :target="item.cartNo" triggers="hover" placement="top">
            <template v-slot:title>Order details</template>
            Name: {{ item.details.name }}<br />Contact Number:
            {{ item.details.contact }}<br />Date: {{ item.details.date }}<br />
            Time: {{ item.details.time }}
          </b-popover>
          <b-button>
            <font-awesome-icon
              icon="trash"
              @click="deleteItem(index, item.subitem)"
            ></font-awesome-icon>
          </b-button>
        </b-button-group>
      </b-col>
      <b-col cols="12"><hr /></b-col>
    </b-row>
    <b-modal ref="delete-confirm" hide-footer title="Confirm delete?">
      <div class="d-block">
        <h3>Item to delete: {{ deleteItemName }}</h3>
        <b-button
          class="mt-3"
          variant="outline-secondary"
          block
          @click="hideModal"
        >
          Cancel
        </b-button>
        <b-button
          class="mt-3"
          variant="outline-danger"
          block
          @click="proceedDeleteItem(deleteIndex)"
        >
          Delete
        </b-button>
      </div>
    </b-modal>
  </b-container>
</template>
<script>
import {
  BContainer,
  BRow,
  BCol,
  BButton,
  BButtonGroup,
  BPopover,
  BModal
} from "bootstrap-vue";
import { FontAwesomeIcon } from "@fortawesome/vue-fontawesome";
export default {
  name: "cart",
  props: ["offsetHt"],
  data: function() {
    return {
      cartItems: null,
      deleteIndex: null,
      deleteItemName: null,
      styleObject: {
        minHeight: null
      }
    };
  },
  components: {
    "b-container": BContainer,
    "b-row": BRow,
    "b-col": BCol,
    "b-button": BButton,
    "b-button-group": BButtonGroup,
    "b-popover": BPopover,
    "b-modal": BModal,
    FontAwesomeIcon
  },
  mounted() {
    if (!localStorage.cart) {
      //init the local storage
      this.cartItems = new Array();
    } else {
      this.cartItems = JSON.parse(localStorage.cart);
    }

    let headerHeight = document.querySelector("#pre-header").offsetHeight;
    let navHeight = document.querySelector("#main-menu").offsetHeight;
    let footerHeight = document.querySelector("#footer").offsetHeight;

    let ht =
      screen.height - (headerHeight + navHeight + footerHeight + this.offsetHt);

    this.styleObject.minHeight = ht + "px";

    
    document.querySelector("#pre-header").scrollIntoView({
      behavior: "smooth",
      alignToTop: true
    });
  },
  methods: {
    col3Weight: weight => {
      return weight !== "" ? `${weight} kg` : "";
    },
    deleteItem(idx, itm) {
      this.deleteIndex = idx;
      this.deleteItemName = itm;
      this.showModal();
    },
    showModal() {
      this.$refs["delete-confirm"].show();
    },
    hideModal() {
      this.$refs["delete-confirm"].hide();
    },
    proceedDeleteItem(idx) {
      this.hideModal();
      this.cartItems.splice(idx, 1);
      localStorage.setItem("cart", JSON.stringify(this.cartItems));

      this.$emit("updateCart");
    },
    testNum(i) {
      if (isNaN(i) || i == "") return "0";
      else return i;
    },
    addPrices(mp, pp) {
      mp = this.testNum(mp);
      pp = this.testNum(pp);
      return parseFloat(mp) + parseFloat(pp);
    }
  }
};
</script>
