<template>
  <div class="container mt-3" v-bind:style="styleObject">
    <b-breadcrumb :items="crumbs"></b-breadcrumb>
    <h2>Palengke</h2>
    <h3>
      <span
        class="d-inline-block mr-3"
        v-for="(mode, index) in displayModes"
        :key="index"
      >
        <a
          href="#"
          class="text-secondary"
          :class="[{ 'text-dark': currentMode == mode.display }]"
          @click.prevent="switchMode(index)"
        >
          <font-awesome-icon :icon="mode.icon"></font-awesome-icon>
        </a>
      </span>
    </h3>
    <transition name="fadeD">
      <div v-show="isGrid">
        <ProductsGrid :items="products" label="Order" @next-step="proceedNext" />
      </div>
    </transition>
    <transition name="fadeD">
      <div v-show="isList">
        <ProductsList :items="products" label="Order" @next-step="proceedNext" />
      </div>
    </transition>
  </div>
</template>
<script>
import ProductsGrid from "@/components/ProductsGrid.vue";
import ProductsList from "@/components/ProductsList.vue";
import { BBreadcrumb } from "bootstrap-vue";
import { FontAwesomeIcon } from "@fortawesome/vue-fontawesome";

export default {
  name: "palengke",
  props: ["products", "offsetHt"],
  components: {
    ProductsGrid,
    ProductsList,
    "b-breadcrumb": BBreadcrumb,
    FontAwesomeIcon
  },
  data: function() {
    return {
      displayModes: [
        { display: "list", icon: "th-list" },
        { display: "grid", icon: "th" }
      ],
      currentMode: "list",
      styleObject: {
        minHeight: null
      }
    };
  },
  computed: {
    isGrid() {
      return this.currentMode == "grid";
    },
    isList() {
      return this.currentMode == "list";
    },
    crumbs() {
      return [
        {
          text: "Home",
          to: { name: "home" }
        },
        {
          text: "Palengke",
          active: true
        }
      ];
    }
  },
  mounted() {
    let headerHeight = document.querySelector("#pre-header").offsetHeight;
    let navHeight = document.querySelector("#main-menu").offsetHeight;
    let footerHeight = document.querySelector("#footer").offsetHeight;

    let ht =
      screen.height - (headerHeight + navHeight + footerHeight + this.offsetHt);

    this.styleObject.minHeight = ht + "px";
    
    document.querySelector("#pre-header").scrollIntoView({
      behavior: "smooth",
      alignToTop: true
    });
  },
  methods: {
    switchMode(index) {
      this.currentMode = this.displayModes[index].display;
    },
    proceedNext(orderParams) {
      let title = orderParams.title;
      switch (title) {
        case "Crab":
          this.$router.replace({ name: "Crab", params: { style: "palengke" } });
          break;
        case "Shrimp":
          this.$router.replace({
            name: "Item",
            params: { item: title, style: "palengke" }
          });
          break;
        //case
      }
    }
  }
};
</script>
<style scoped>
.fadeD-enter-active,
.fadeD-leave-active {
  transition: all 0.5s ease;
}
.fadeD-enter,
.fadeD-leave-active {
  opacity: 0;
}
</style>
