import Vue from "vue";
import VueRouter from "vue-router";
import Home from "../views/Home.vue";
import OrderSummary from "../views/OrderSummary.vue";
import Palengke from "../views/Palengke.vue";
import Paluto from "../views/Paluto.vue";
import Cart from "../views/Cart.vue";
import Crab from "../views/Crabs.vue";
import CrabSummary from "../views/CrabSummary.vue";
import Item from "../views/Items.vue";
import ItemSummary from "../views/ItemSummary.vue";
import DishRestaurantSummary from "../views/DishRestaurantSummary.vue";

Vue.use(VueRouter);

const routes = [
  {
    path: "/",
    name: "home",
    component: Home
  },
  {
    path: "/palengke",
    name: "Palengke",
    component: Palengke
  },
  {
    path: "/paluto",
    name: "Paluto",
    component: Paluto
  },
  {
    path: "/:style/crab",
    name: "Crab",
    component: Crab
  },
  {
    path: "/:style/:item",
    name: "Item",
    component: Item
  },
  {
    path: "/:style/crab/:id",
    name: "CrabSummary",
    component: CrabSummary
  },
  {
    path: "/:style/:item/:id",
    name: "ItemSummary",
    component: ItemSummary
  },
  {
    path: "/:style/:item/:id/dishrestaurant/:orderDetails",
    name: "DishRestaurantSummary",
    component: DishRestaurantSummary
  },
  {
    path: "/ordersummary/:style/:product/:id",
    name: "OrderSummary",
    component: OrderSummary
  },
  {
    path: "/cart",
    name: "Cart",
    component: Cart
  }
];

const router = new VueRouter({
  routes
});

export default router;
