<template>
  <div id="app">
    <CartHeader :itemsInCart="itemsInCart" />
    <Navigation />
    <router-view
      :bilao="bilao"
      :restaurants="restaurants"
      :styles="style"
      :products="productsFinal"
      :crabs="crabs"
      :items="items"
      :weightKg="weightKg"
      :offsetHt="0"
      @updateCart="updateCartItems"
    />
    <Footer />
  </div>
</template>

<script>
import CartHeader from "@/components/CartHeader.vue";
import Navigation from "@/components/Navigation.vue";
import Footer from "@/components/Footer.vue";

import bilaoList from "../src/assets/json/bilao.json";
import restaurantList from "../src/assets/json/restaurant.json";
import productsList from "../src/assets/json/products.json";
import crabList from "../src/assets/json/crabs.json";
import itemList from "../src/assets/json/items.json";

export default {
  name: "App",
  data: function() {
    return {
      bilao: bilaoList.bilao,
      restaurants: restaurantList.restaurants,
      items: itemList,
      style: ["palengke", "paluto"],
      products: ["Bilao", "Crabs", "Shrimp"],
      weightKg: [0.5, 1, 1.5, 2, 2.5, 3, 3.5],
      cartItems: [],
      crabs: crabList.crabs,
      itemsInCart: null
    };
  },
  components: {
    CartHeader,
    Navigation,
    Footer
  },
  computed: {
    productsFinal: function() {
      return productsList.products;
    }
  },
  mounted() {
    if (localStorage.getItem("cart")) {
      try {
        this.cartItems = JSON.parse(localStorage.getItem("cart"));
      } catch (e) {
        localStorage.removeItem(this.cartItems);
      }
    }

    if (localStorage.cart)
      this.itemsInCart = JSON.parse(localStorage.cart).length;
    else this.itemsInCart = 0;
  },
  methods: {
    updateCartItems() {
      if (localStorage.cart)
        this.itemsInCart = JSON.parse(localStorage.cart).length;
      else this.itemsInCart = 0;
    }
  }
};
</script>

