<template>
  <div>
    <b-card
      no-body
      class="overflow-hidden"
      v-for="item in items"
      :key="item.id"
      style="max-width:550px; margin-bottom:10px"
    >
      <b-row no-gutters>
        <b-col cols="6" md="4">
          <b-card-img
            :src="require(`@/assets/images/${item.img}`)"
            class="rounded-0"
          >
          </b-card-img>
        </b-col>
        <b-col cols="6" md="8" class="p-2">
          <b-card-body :title="item.title">
            <b-card-text>
              <p>
                <a
                  href="#"
                  class="card-link"
                  @click.prevent="proceedOrder(item.id, item.title, item.img)"
                >
                  {{ label }} {{ item.title }}
                </a>
              </p>
            </b-card-text>
          </b-card-body>
        </b-col>
      </b-row>
    </b-card>
  </div>
</template>
<script>
import {
  BRow,
  BCol,
  BCard,
  BCardBody,
  BCardText,
  BCardImg
} from "bootstrap-vue";
export default {
  name: "productslist",
  props: ["items", "label"],
  components: {
    "b-row": BRow,
    "b-col": BCol,
    "b-card": BCard,
    "b-card-body": BCardBody,
    "b-card-text": BCardText,
    "b-card-img": BCardImg
  },
  methods: {
    proceedOrder(id, title, img) {
      let orderParams = { id: id, title: title, img: img };
      this.$emit("next-step", orderParams);
    }
  }
};
</script>
