<template>
  <b-container class="mt-3" v-bind:style="styleObject">
    <b-breadcrumb :items="crumbs"></b-breadcrumb>
    <b-row>
      <b-col>
        <transition name="fadeD">
          <DishSelect
            v-if="isDish"
            :itmOrder="itemOrder"
            :itmOrderDetails="itemOrderDetails"
            :dishes="dishList.list"
            @submit-dish="submitDish"
          />
        </transition>
        <transition name="fadeD">
          <RestaurantSelect
            v-if="isRestaurant"
            :itmOrder="itemOrder"
            :selectedDish="selectedDish"
            :restaurants="restaurants"
            @proceed-summary="proceedSummaryDetails"
          />
        </transition>
        <transition name="fadeD">
          <DRSummaryDetails
            v-if="isDRSummary"
            :itmOrder="itemOrder"
            :selectedDish="selectedDish"
            :availableKg="availableKg"
            :kgOrder="kgOrder"
            :selectedRestaurant="jsonRestaurant"
            @pass-details="passDetails"
          />
        </transition>
      </b-col>
    </b-row>
  </b-container>
</template>
<script>
import { BContainer, BRow, BCol } from "bootstrap-vue";
import DishSelect from "@/components/DishSelect.vue";
import RestaurantSelect from "@/components/RestaurantSelect.vue";
import DRSummaryDetails from "@/components/DRSummaryDetails.vue";
import { BBreadcrumb } from "bootstrap-vue";
import dish from "@/assets/json/dish.json";

export default {
  name: "dishresturantsummary",
  props: ["styles", "products", "offsetHt", "crabs", "items", "restaurants"],
  data: function() {
    return {
      style: this.$route.params.style,
      item: this.$route.params.item,
      productId: this.$route.params.id,
      isDish: true,
      isRestaurant: false,
      isDRSummary: false,
      styleObject: {
        minHeight: null
      },
      itemsList: this.items.items,
      itemsName: this.items.itemsName,
      itemOrderDetails: JSON.parse(this.$route.params.orderDetails),
      dishes: dish.dish,
      selectedDish: null,
      availableKg: null,
      kgOrder: null,
      jsonRestaurant: null
    };
  },
  created() {
    let prodList = this.products.map(p => p.title.toLowerCase());

    if (
      !(this.styles.indexOf(this.style) >= 0) ||
      !(prodList.indexOf(this.item.toLowerCase()) >= 0)
    ) {
      this.$router.replace({ name: "home" });
    }
  },
  mounted() {
    switch (this.item.toLowerCase()) {
      case "crab":
        if (!this.checkExistProduct(this.crabs, this.productId)) {
          this.$router.replace({ name: "home" });
        }

        break;
      case "shrimp":
        if (!this.checkExistProduct(this.itemsFinalList, this.productId)) {
          this.$router.replace({ name: "home" });
        }

        break;
      default:
        //did not pass any of the test
        this.$router.replace({ name: "home" });
    }

    let headerHeight = document.querySelector("#pre-header").offsetHeight;
    let navHeight = document.querySelector("#main-menu").offsetHeight;
    let footerHeight = document.querySelector("#footer").offsetHeight;

    let ht =
      screen.height - (headerHeight + navHeight + footerHeight + this.offsetHt);

    this.styleObject.minHeight = ht + "px";
  },
  computed: {
    dishList() {
      return this.dishes.find(ds => ds.name == this.item);
    },
    crumbs() {
      let cArr = [{ text: "Home", to: { name: "home" } }];

      cArr.push({ text: "Paluto", to: { name: "Paluto" } });

      if (this.item == "Crab") {
        cArr.push({
          text: this.item,
          to: {
            name: "Crab",
            params: { style: "paluto" }
          }
        });
      } else {
        cArr.push({
          text: this.item,
          to: {
            name: "Item",
            params: {
              item: this.item,
              style: "paluto"
            }
          }
        });
      }

      if (this.item == "Crab") {
        //crab summary
        cArr.push({
          text: this.itemOrder.title + " Order",
          to: {
            name: "CrabSummary",
            params: {
              style: this.style,
              id: this.productId
            }
          }
        });
      } else {
        cArr.push({
          text: this.itemOrder.title + " Order",
          to: {
            name: "ItemSummary",
            params: {
              item: this.item,
              style: this.style,
              id: this.productId
            }
          }
        });
      }

      cArr.push({
        text: this.itemOrder.title + " Dish and Restaurant",
        active: true
      });

      return cArr;
    },
    itemOrder() {
      let arrObj = null;
      switch (this.item.toLowerCase()) {
        case "crab":
          arrObj = this.crabs;
          break;
        case "shrimp":
          arrObj = this.itemsFinalList;
          break;
      }

      return arrObj[arrObj.findIndex(el => el.id == this.productId)];
    },
    itemsFinalList() {
      return this.items.items[this.items.itemsName.indexOf(this.item)][
        this.item
      ];
    }
  },
  components: {
    "b-container": BContainer,
    "b-row": BRow,
    "b-col": BCol,
    "b-breadcrumb": BBreadcrumb,
    DishSelect,
    RestaurantSelect,
    DRSummaryDetails
  },
  methods: {
    checkExistProduct(list, productId) {
      return list.some(el => {
        return el.id == productId;
      });
    },
    proceedAsPalengke() {
      sessionStorage.clear();

      if (this.item == "Crab") {
        sessionStorage.crabOrderDetails = JSON.stringify(this.itemOrderDetails);
      } else {
        sessionStorage.itemOrderDetails = JSON.stringify(this.itemOrderDetails);
      }

      this.$router.replace({
        name: "OrderSummary",
        params: { style: "palengke", product: this.item, id: this.productId }
      });
    },
    submitDish(objParam) {
      this.availableKg = objParam.availableKg;
      this.kgOrder = objParam.kgOrder;
      this.selectedDish = JSON.stringify(objParam.selectedDish);

      if (this.kgOrder == 0) {
        this.proceedAsPalengke();
      } else {
        this.isDish = false;
        this.isRestaurant = true;
      }
    },
    proceedSummaryDetails(objParam) {
      let obj = objParam;
      obj.title = objParam.title;
      obj.img = objParam.img;

      this.jsonRestaurant = JSON.stringify(obj);

      this.isDish = false;
      this.isRestaurant = false;
      this.isDRSummary = true;
    },
    passDetails(objParam) {
      let objRestaurant = JSON.parse(this.jsonRestaurant);
      let restaurantTitle = objRestaurant.title;
      let mode =
        objParam.mode == "dine-in" ?
          `Dine in at ${restaurantTitle}` :
          "Deliver to customer address";

      if (!localStorage.cart) {
        //init the local storage
        localStorage.cart = JSON.stringify(new Array());
      }

      let cart = JSON.parse(localStorage.cart);

      JSON.parse(this.selectedDish).forEach(dsh => {
        let itemCart = Object.create(null);
        itemCart.cartNo = "cid-" + cart.length;
        itemCart.style = this.style;
        itemCart.item_id = this.itemOrder.id;
        itemCart.item = this.item;
        itemCart.subitem = this.itemOrder.title;
        itemCart.prod_details = this.itemOrderDetails.prod_details;
        itemCart.weight = dsh.kg;
        itemCart.market_price = dsh.mp;
        itemCart.img = dsh.img;
        itemCart.restaurant = restaurantTitle;
        itemCart.paluto = dsh.dish;
        itemCart.mode = dsh.dish + ", " + mode;
        itemCart.details = {
          name: objParam.name,
          contact: objParam.contact,
          date: objParam.date,
          time: objParam.time,
          special_instruction: objParam.si
        }
        itemCart.paluto_price = dsh.pp;

        cart.push(itemCart);
      });

      if (parseFloat(this.availableKg) > 0) {
        //as palengke
        let itemCart = Object.create(null);
        itemCart.cartNo = "cid-" + cart.length;
        itemCart.style = this.style;
        itemCart.item_id = this.itemOrder.id;
        itemCart.item = this.item;
        itemCart.subitem = this.itemOrder.title;
        itemCart.prod_details = this.itemOrderDetails.prod_details;
        itemCart.weight = parseFloat(this.availableKg);
        itemCart.market_price = parseFloat(this.availableKg) * parseFloat(this.itemOrderDetails.unitPrice);
        itemCart.img = this.itemOrder.img;
        itemCart.restaurant = restaurantTitle;
        itemCart.paluto = "";
        itemCart.mode =
          objParam.mode == "dine-in" ?
          `Pick up at ${restaurantTitle}` :
          "Deliver to customer address";
        itemCart.details = {
          name: objParam.name,
          contact: objParam.contact,
          date: objParam.date,
          time: objParam.time
        }
        itemCart.paluto_price = "";

        cart.push(itemCart);
      }

      localStorage.cart = JSON.stringify(cart);

      this.$emit("updateCart");

      this.$router.replace({ name: "Cart" });
    }
  }
};
</script>
<style scoped>
.fadeD-enter-active,
.fadeD-leave-active {
  transition: all 0.5s ease;
}
.fadeD-enter,
.fadeD-leave-active {
  opacity: 0;
}
</style>
