import Vue from "vue";
import App from "./App.vue";
import router from "./router";
import BootstrapVue from "bootstrap-vue";

import "../node_modules/jquery/dist/jquery.slim.min";
import "../node_modules/popper.js/dist/popper.min";
import "../node_modules/bootstrap/dist/js/bootstrap";

import { library } from "@fortawesome/fontawesome-svg-core";

import {
  faHome,
  faFileAlt,
  faCartPlus,
  faNewspaper,
  faMapMarkerAlt,
  faLink,
  faListUl,
  faRandom,
  faUndo,
  faStar,
  faEnvelope,
  faSlidersH,
  faThList,
  faTh,
  faShoppingBasket,
  faTrash,
  faInfoCircle
} from "@fortawesome/free-solid-svg-icons";

library.add(
  faHome,
  faFileAlt,
  faCartPlus,
  faNewspaper,
  faMapMarkerAlt,
  faLink,
  faListUl,
  faRandom,
  faUndo,
  faStar,
  faEnvelope,
  faSlidersH,
  faThList,
  faTh,
  faShoppingBasket,
  faTrash,
  faInfoCircle
);

Vue.config.productionTip = false;

new Vue({
  router,
  BootstrapVue,
  render: h => h(App)
}).$mount("#app");
